import React from "react"

export const IconViewer = () => {
  // @ts-ignore

  return (
    <>
      <div style={{ position: 'fixed', left: 0, right: 0, top: 0, bottom: 0, backgroundColor: 'white' }}>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: 10, padding: 30 }}>
          {Object.keys(icons)?.map(i => (
            <div className='d-center' style={{ width: 120, height: 80 }} key={"icon-" + i}>
              <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 5 }}>
                <Icon icon={i} size={48} fill="var(--color-secondary)" />
                <span>{i}</span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  )
}

interface IconProps {
  className?: string
  fill?: string
  size?: number
  height?: number
  marginLeft?: number
  margin?: number
  icon: string
}

export const Icon = ({ className, fill, size, height, icon, marginLeft, margin }: IconProps) => (
  <div className={className} style={{ display: 'flex', color: fill, width: size || 16, height: height || size || 16, marginLeft: marginLeft || 0, marginRight: margin || 0 }}>
    {icons[icon]}
  </div>
)

